<template>
  <div class="mydetial">
    <el-table
      :data="tableData"
      :row-style="rowstyle"
      :cell-style="rowstyle"
      :header-row-style="headstyle"
      :header-cell-style="headstyle"
      style="width: 100%;  background-color: transparent !important;"
    >
      <el-table-column prop="dataKey" width="180"></el-table-column>
      <el-table-column prop="nameValue" width="180"></el-table-column>
    </el-table>
    <el-button type="primary" size="small" class="btn" v-if="status">{{btnvalue}}</el-button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [{
        dataKey: '这是日期',
        nameValue: '我是谁?'
      }, {
        dataKey: '这是日期',
        nameValue: '我是谁?'
      }, {
        dataKey: '这是日期',
        nameValue: '我是谁?'
      }, {
        dataKey: '这是日期',
        nameValue: '我是谁?'
      }, {
        dataKey: '这是日期',
        nameValue: '我是谁?'
      }],
      btnvalue: '拨打',
      status: true
    }
  },
  props: ['info'],
  methods: {
    loadInfo () {
      this.tableData = []
      this.info.forEach(item => {
        let info = {
          dataKey: item.dataKey,
          nameValue: item.nameValue
        }
        this.tableData.push(info)
        if (item.dataKey === '监控名称') {
          this.btnvalue = '播放'
        }
      })
    },
    rowstyle ({ row, rowIndex }) {
      return 'background-color: transparent; border: 0; color: #88d6ff; row:hover : black'
    },
    headstyle () {
      return 'background-color: transparent; border: 0; color: #00f1e5;'
    }
  },
  mounted () {
    this.loadInfo()
  }
}
</script>

<style lang="stylus">
.mydetial {
  text-align: center;

  .btn {
    margin-top: 20px;
  }
}

.el-table::before {
  height: 0;
}
</style>



