    async goCommand (index) {
      if (index === 2) {
        // 到预案响应页面
        this.$router.push('/scheme/?eventId=' + this.eventId)
      } else if (index === 3) {
        // 到指挥调度页面
        this.$router.push('/command/?eventId=' + this.eventId)
      } else if (index === 4) {
        this.$confirm('是否结束指挥?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          customClass: 'messageBox',
          type: 'warning'
        }).then(async () => {
        let item = { DisposeState: 4, endTime: this.getNowTime() }
          let conditions = [{ Property: 'id', Oper: 'eq', Value: this.eventId }]
          var updateEnd = builderParams.update('event', item, conditions)
          updateEnd.DataBase = 'duty'
          // 投大屏 上报事件处理状态
          const configuration = await get({url: 'static/config.json', data: {}})
          let eventProcessReport = configuration.visual.baseUrl + configuration.visual.eventProcessReport
          let TableData = {
            // 编号
            bh: this.getuuid(),
            // 系统名称
            xtmc: 'zhdd',
            //调用地址
            jkdz: eventProcessReport,
            // 调用类型
            ajaxType: 'POST',
            // 请求参数
            param: JSON.stringify({ id: this.eventId, status: 0 }),
            // 状态
            state: 0,
            // 入库时间戳
            rksjc: this.getNowTime(),
            // 更新时间
            gxsj: null,
            // 操作人编号
            gxrbh: '',
            // 操作人
            gxr: ''
          }
          var saveItem = builderParams.save('quartz', TableData)
          saveItem.DataBase = 'duty'
          const taskResult = await post({ url: builderParams.execUrl(), data: saveItem })
          const result = await post({ url: builderParams.execUrl(), data: updateEnd })
          if (result.Result === 0) {
            this.$message({
              type: 'success',
              message: '指挥已结束，关闭当前页面'
            })
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
      }
    },
    async getEventInfo (eventId) {
      // 查询事件信息
      let conditions = [{ Property: 'Id', Oper: 'eq', Value: eventId }]
      let item = builderParams.get('Event', '', conditions)
      item.DataBase = 'duty'
      const event = await post({ url: builderParams.getUrl(), data: item })
      if (event.Result === 0) {
        if (event.Data.length > 0) {
          let eventInfo = event.Data[0]
          // 倒计时
          if (eventInfo.Endtime) {
            this.startTime = '指挥结束'
          } else {
            this.startTime = new Date(eventInfo.Eventtime).getTime()
          }
          this.eventInfo[0].value = eventInfo.Eventtime
          this.eventInfo[1].value = eventInfo.Ajlbname
          this.eventInfo[2].value = eventInfo.Address
          this.eventInfo[3].value = eventInfo.Content
          this.eventInfo[4].value = eventInfo.Sourcetype
          this.eventInfo[0].value = this.printTime
          eventInfo.Eventtime = this.eventInfo[0].value
          this.sendEvent = eventInfo
          this.drawCircle(eventInfo.Dwxzb, eventInfo.Dwyzb)
          // 根据 事件处置状态 跳转页面
          if (eventInfo.Disposestate === '2') {
            // 预案响应
            // 到预案响应页面
            this.$router.push('/scheme/?eventId=' + this.eventId)
          } else if (eventInfo.Disposestate === '3') {
            // 到指挥调度页面
            this.$router.push('/command/?eventId=' + this.eventId)
          }
          // 先期处置信息
          this.task = []
          eventInfo.Measure.split('\\n').forEach(item => {
            this.task.push({
              name: item,
              state: 1
            })
          })
          // 查询 处置规程
          let conditions = [{ Property: 'caseclass', Oper: 'eq', Value: eventInfo.Ajlb }]
          var flowItem = builderParams.get('Disposalprocess', '', conditions)
          const result = await post({url: builderParams.getUrl(), data: flowItem})       
          if (result.Result === 0) {
            if (result.Data.length > 0) {
              result.Data[0].Content.split('\n').forEach(item => {
                this.rulesExplain.push({
                  name: item
                })
              })
            }
          }
        }
      }
    },
    // 资源接口 机构，队伍，仓库， 物资   机构暂不支持gis空间查询
    async loadGood (type, obj) {
      // 参数： 物资类型(jg, team, store, goods ) 坐标对象
      this.tableData = []
      if (!type) return
      let table = {
        longitude: 12952105.7556492,
        latitude: 4856953.53616151,
        radius: 10000000,
        tableNameType: type
        // page: 1,
        // pageSize: 5
      }
      let configuration = await get({url: 'static/config.json', data: {}})
      console.log(configuration)
      let getGisReSourceUrl = configuration.resource.baseUrl + configuration.resource.getGisReSourceUrl
      if (type !== 'jg') {
        
        let goodsResult = await get({url: getGisReSourceUrl, data: table})
        
        if (goodsResult.context.status === 200) {
          if (goodsResult.data.totalItems > 0) {
            if (type === 'goods') {
              goodsResult.data.resultList.forEach(item => {
                this.tableData.push({
                  date: item.goodsName,
                  name: '',
                  address: item.storeNum + '' + item.unit
                })
              })
            }
            if (type === 'team') {
              goodsResult.data.resultList.forEach(item => {
                this.tableData.push({
                  date: item.teamName,
                  name: '',
                  address: item.teamNum + '人'
                })
              })
            }
            if (type === 'store') {
              goodsResult.data.resultList.forEach(item => {
                this.tableData.push({
                  date: item.storeName,
                  name: '',
                  address: item.address
                })
              })
            }
          }
        }
      }
      if (type === 'jg') {
        // 运输，通讯，医疗，企业属于机构类型
        let parmeter = {
          longitude: 12952105.7556492,
          latitude: 4856953.53616151,
          radius: 10000000,
          tableNameType: 'communication'
          // page: 1,
          // pageSize: 5
        }
        let result = await get({ url: getGisReSourceUrl, data: parmeter })
        if (result.context.status === 200) {
          if (result.data.totalItems > 0) {
            result.data.resultList.forEach(item => {
              this.tableData.push({
                date: item.communicationName,
                name: '',
                address: item.address
              })
            })
          }
        }
        result = {}
        parmeter.tableNameType = 'transport'
        result = await get({ url: getGisReSourceUrl, data: parmeter })
        if (result.context.status === 200) {
          if (result.data.totalItems > 0) {
            result.data.resultList.forEach(item => {
              this.tableData.push({
                date: item.transportName,
                name: '',
                address: item.address
              })
            })
          }
        }
        result = {}
        parmeter.tableNameType = 'company'
        result = await get({ url: getGisReSourceUrl, data: parmeter })
        if (result.context.status === 200) {
          if (result.data.totalItems > 0) {
            result.data.resultList.forEach(item => {
              this.tableData.push({
                date: item.companyName,
                name: '',
                address: item.address
              })
            })
          }
        }
        result = {}
        parmeter.tableNameType = 'medical'
        result = await get({url: getGisReSourceUrl, data: parmeter })
        if (result.context.status === 200) {
          if (result.data.totalItems > 0) {
            result.data.resultList.forEach(item => {
              this.tableData.push({
                date: item.medicalName,
                name: '',
                address: item.address
              })
            })
          }
        }
      }
    },
    // 更多
    more () {
      let id = this.$layer.iframe({
        content: {
          content: Mcontent, // 传递的组件对象
          parent: this, // 当前的vue对象
          data: { eventInfo: this.sendEvent }// props
        },
        shade: false, // 是否显示遮罩
        area: ['400px', '420px'],
        offset: ['700', '350'],
        title: '事件详情'
      })
      this.layerId = id
    },
    // test
    popit (obj) {
      let id = this.$layer.iframe({
        content: {
          content: Resdetial, // 传递的组件对象
          parent: this, // 当前的vue对象
          data: { eventInfo: this.sendEvent, info: obj }// props
        },
        shade: false, // 是否显示遮罩
        area: ['400px', '400px'],
        offset: ['950', '430'],
        title: '资源详情'
      })
      this.layerId = id
    },
    initEventMap () {
      // this.$map.drawCircle({
      //   x: 117.2009,
      //   y: 39.2243,
      //   radius: 1500,
      //   layerId: this.$map.constslayer.cacheLayer,
      //   isCenter: false,
      //   style: {
      //     color: '#000000',
      //     fontSize: 12,
      //     lineStyle: 'solid',
      //     borderWidth: 1,
      //     borderColor: '#f82026',
      //     background: [248, 32, 38, 0.65],
      //     fillStyle: 'cross'
      //   },
      //   onsuccess: (wkt, opt) => {
      //     this.$map.drawPointToMap({
      //       x: opt.x,
      //       y: opt.y,
      //       ico: { name: './static/arcgismap/assets/img/marker.png', height: 30, width: 22 },
      //       layerId: this.$map.constslayer.alarmLayer,
      //       isCenter: true,
      //       click: () => { }
      //     })
      //   }
      // })
    }
  },
  watch: {
    curr: function (value) {
      if (value === 0) {
        // 物资
        this.loadGood('goods')
      }
      if (value === 1) {
        // 队伍
        this.loadGood('team')
      }
      if (value === 3) {
        // 仓库
        this.loadGood('store')
      }
      if (value === 2) {
        // 机构
        this.loadGood('jg')
      }
    }
  },
  mounted () {
    // this.$route.query.id  事件Id
    // 测试事件id， 为undeifined 时 默认赋值编号为 abcd
    if (this.$route.query.eventId) {
      this.eventId = this.$route.query.eventId
    } else {
      this.eventId = '0395d345-bb26-d162-590f-98186b7bb59a'
    }
    this.getEventInfo(this.eventId)
    // 查询周边资源
    this.loadGood('jg')
  },
  components: {
    'map-tools': Maptools
  }
}
</script>
